import {connect} from "react-redux";
import React, {Component} from "react";
import PropTypes from "prop-types";
import {getCvMagnumCheckoutErrors} from "eshop/modules/checkout/selectors";
import {dismissCvErrors, gotoPreviousCheckoutStep, gotoNextCheckoutStep} from "eshop/modules/checkout/actions/app";
import {OraButton} from "eshop/components/OraCommonComponents";
import OraModal from "eshop/modules/core/components/ui/Modal";
import {getMaxBundleNo } from "eshop/modules/cart/selectors"
import {removeFromCartAndRedirect, handleRepayment} from "eshop/modules/cart/actions/cart";
import {getSalesChannel} from "eshop/modules/auth/selectors/authorization"

import TableErrorSection from "eshop/modules/checkout/components/modals/customerVerification/TableErrorSection"

class MagnumCustomerVerificationModalView extends Component {

    constructor(props) {
        super(props);
        this.state =  {currency: "zł",checkboxAccepted: false, showPaymentWarning: false};
    }

    componentDidMount() {
        let $helpTooltip = document.getElementById("magnum-deposit-modal-help-tooltip");
        if(!$helpTooltip) {
            console.warn("elem with id: \"magnum-deposit-modal-help-tooltip\" not found");
        } else {
            OPL.UI.loadModules($helpTooltip, [{
                path: 'core/modules/tooltips',
                options: {"mouseoverMinWidth":0, "triggerEvent": "mouseover"}
            }]);
        }
    }

    render() {
        const {errors} = this.props;
        let hasReasonsData = errors && errors.length > 0 && errors[0].details.bundles.length > 0;
        /* always render header so tooltip is always present for activateHelpTooltip function  */
        let $element = (<OraModal showClose={true} open={hasReasonsData} onClose={this.props.dismiss} size="medium">
            {this.renderHeader()}
            {hasReasonsData && <div>
                {renderSeparatorLine()}
                {this.renderMessagesForOffers()}
             	{this.renderDepositSum()}
             	{this.renderPaytel()}
                {this.renderFooterButtons()}
                </div>
            }
            </OraModal>
        )
        return $element;
    }
    handleRepaymentButton(){
        if(!this.state.checkboxAccepted){
            this.setState({showPaymentWarning: true});
        }else{
            this.props.handleRepayment();
        }
    }
    gotoNextCheckoutStepButton(){
        if(this.isPaytelAcceptable() && !this.state.checkboxAccepted){
            this.setState({showPaymentWarning: true});
        }else{
            this.props.gotoNextCheckoutStep();
        }
    }

    handleCheckboxChange(event){
        this.setState({checkboxAccepted : event.target.checked});
        this.setState({showPaymentWarning : !event.target.checked});
    }
    isPaytelAcceptable(){
        return (this.props.channels.sales=='POS' || this.props.channels.sales=='AKA') && this.props.errors.some((cvVer) => {return !!cvVer.paytelAcceptable});
    }
    renderPaytel(){
        return  ( <div className="u-padding-all-s u-small-padding-s">
                                         <table className="u-table-fixed">
                                         <tbody>
                                              {this.isPaytelAcceptable() &&
                                              <tr className="">
                                                 <td className="" colSpan="2">
                                                  <label className="o-checkbox opl-checkbox opl-checkbox--black">
                                                     <input type="checkbox" onChange={event => {this.handleCheckboxChange(event)}}/> <span className="o-ci"></span>
                                                        <span className="o-ci-label"><strong>{"Kontynuuj zamówienie ze spłatą zadłużenia"} </strong></span>
                                                  </label>
                                                 </td>
                                             </tr>}
                                              {this.isPaytelAcceptable() &&
                                              <tr className="">
                                                 <td colSpan="2" className="u-padding-top-s">
                                                         {"Zaznacz powyższe i kontynuuj jeśli Klient zgadza się na dokonanie spłaty zadłużenia na miejscu przez terminal Paytel"}
                                                 </td>
                                             </tr>}
                                             {this.isPaytelAcceptable() && this.state.showPaymentWarning &&
                                              <tr className="">
                                                 <td className="u-padding-top-l" colSpan="2">
                                                 <div className="o-icon-text g-icon g-icon--error g-icon--before g-icon--xs-s g-redf-c ">
                                                     <p className="o-icon-text__text u-font-bold opl-msg--error">Akceptacja jest wymagana do realizacji zamówienia.</p>
                                                 </div>
                                                 </td>
                                              </tr>
                                             }
                                          </tbody>
                                          </table>
                                      </div>)
    }
    renderMessagesForOffers = () => { 
        const offers = this.props.errors[0].details.bundles;
        return  (<div id="messages-for-offers"> 
            {
                offers.map( (offer, i) => {
                    return (<div key={i}>
            			<div  key={i + "-title"} className="u-padding-top">
                            <div className="u-font-bold">
                                Pakiet {offer.title}
                            </div>
                            <div>
                                {this.props.descriptions.loyaltyCaption || "Umowa na "} {offer.loyaltyDuration} miesiące
                            </div>
                        </div>
                        <div  key={i + "-reason"}>
                        {
                            offer.entries.map( function(reasonData, j) {
                            		return this.renderMessage(reasonData, i + "_" + j);
            	            }, this)
            	        }</div>
                    </div>)
                })
            }
        </div>)   
    }
    
    renderMessage = (reasonData, idIndex) => {
        const {descriptions} = this.props;
        return (
            <div>
                <TableErrorSection key={"reasonSection_" + idIndex} reasonData={reasonData}
                    descriptions={descriptions}/>
                {renderSeparatorLine()}
            </div>
        )
    }

    renderErrorInfo() {
        const {descriptions} = this.props;
        return (
            <div>
                <div className="l-page-row u-padding-top-s u-no-padding-left  u-no-margin-left">
                    <div className="l-col l-col-12 u-large-left u-medium-left">
                        <div className="u-left">
                             <span>descriptions.negativeVerificationHeader</span>
                        </div>
                    </div>
                </div>
                 <div className="l-page-row u-no-padding-left  u-no-margin-left">
                    <div className="l-col l-col-12 u-large-left u-medium-left">
                        <div className="u-left">
                             <span>{descriptions.negativeVerificationHeaderLine2}</span>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

    renderInfo = (header, hearerLine2, descrition, depositDescription, tolltipContent) => { 
        const category = (this.props.errors && this.props.errors.length) ? this.props.errors[0].category : null;
        const depositSum = (this.props.errors && this.props.errors.length) ? this.props.errors[0].details.depositSum : null;
        return ( 
             <div className="u-padding-top-s">
                 <div className="sg sg-section-header kss-title g-gray2-bg u-padding-top-s u-padding-bottom-s u-padding-left-s u-padding-right-s">
                    <span   data-js-module="core/modules/tooltips" 
                            data-js-options="{&quot;side&quot;: &quot;top&quot;, &quot;trigger&quot;: &quot;hover&quot;}">
                                <h5 className="u-margin-s ">
                                    <span className="u-padding-right-s">{header}</span>
                                    <span data-js-module="core/modules/tooltips" id="magnum-deposit-modal-help-tooltip" >
                                        <a href="#" data-type="no-click" data-tooltip-trigger-event="mouseover" className="o-tooltip__trigger" 
                                                hidden={category !== "CV_MAGNUM_WITH_DEPOSIT"}>
                                            <span className="g-icon g-icon--info g-icon--xs"/>
                                        </a>
                                        <span className="o-tooltip__content"  hidden="true"> {(tolltipContent)} </span>
                                    </span>
                                </h5> 
                                {!!hearerLine2 && <div className="u-padding-s">{hearerLine2}</div>}

                    </span>
                </div> 
                <div className="l-page-row u-padding-top-m u-no-padding-left  u-no-margin-left u-no-padding-right">
                    <div className="l-col l-col-9 u-left u-no-padding-left  u-no-margin-left">
                        <span>{(descrition) || "Produkty do zamówienia"}</span>
                    </div>
                    {!!depositSum && 
                        <div className="l-col l-col-3 u-right u-no-padding-right u-text-right"> 
                            <span>{depositDescription ||"Wysokość kaucji"}</span>
                        </div>
                    }
                </div>
            </div>
        )
    }

    renderHeader = () => {
        const category = (this.props.errors && this.props.errors.length) ? this.props.errors[0].category : "";
        const {descriptions} = this.props;
        let {description} = this.props;
        let iconClass, title, header, headerLine2;

        switch(category) {
            case "CV_MAGNUM":
                iconClass = " g-icon--error g-redf-c ";
                title = descriptions.negativeVerificationResultTitle || "Zamówienie nie może być zrealizowane";
                description = descriptions.negativeVerification
                header = descriptions.negativeVerificationHeader || "Negatywnie zweryfikowane";
                headerLine2 = descriptions.negativeVerificationHeaderLine2;
                break;
            case "CV_MAGNUM_WITH_DEPOSIT":
                iconClass = " g-icon--warn g-yellowf-c ";
                title = descriptions.verificationResultTitle || "Wynik weryfikacji technicznej";
                header = descriptions.depositRequiredVerification || "Wymagana kaucja";
                break;
            default:
               iconClass = "opl-msg opl-msg--error opl-msg--context";
               title = descriptions.verificationResultTitle || "Wynik weryfikacji technicznej" ;
               header = "Dokonano weryfikacji technicznej";
               description = "Zweryfikowano";
               break;
        }

        return (
            <div >
                <div className="u-font-standard u-margin-l opl-modal-title-space">
                    <div className={"u-small-hide o-icon-text g-icon  g-icon--before g-icon--m " + iconClass}>
                        <span className="h3 u-padding-top-s"> {title} </span>
                    </div>
                </div>
                {this.renderInfo(header, headerLine2,  description, descriptions.depositValue, descriptions.tooltipDepositDefinition)} 
    	    </div>
		)
    }

    renderDepositSum = () => { 
        const depositSum = this.props.errors[0].details.depositSum;
        if(!!depositSum) {
            return (
                <div className="l-page-row u-no-padding-right">
                    <div className="l-col l-col-12 u-large-right u-medium-right u-padding-top-m  u-no-padding-right">
                        <div className="u-right u-font-bold u-text-right">
                            <span>Razem: </span>
                                <span className="u-right u-padding-left-s g-brand1-c">
                                {depositSum} {this.state.currency}
                                </span> 
                        </div>
                    </div>
                </div>
            ) 
        }
    }

    renderFooterButtons(){
        let offsetClass = (this.canAcceptVerification() || this.isPaytelAcceptable()) ? "" : "large-offset-by-6";
        return (
            <div className="l-row u-margin-top-l">
                {!this.props.goToCartSummary ? null : [
                    <div key="back-to-cart" className="l-col l-col-small-3 l-col-medium-3 l-col-3 u-large-left u-medium-left">
                        <OraButton onClick={this.props.goToCartSummary.bind(this)} className="u-w-100" type="secondary">
                            {this.props.descriptions.backToCartButton || "Wróć do koszyka"}
                        </OraButton>
                    </div>,
                    <div key="operations" className="large-offset-by-3 l-col l-col-small-6 l-col-medium-4 l-col-6 u-small-padding-l u-large-right u-medium-right">
                        <div className="l-row">
                           <div className={offsetClass + " l-col l-col-6"}>
                                <OraButton onClick={this.props.removeFromCartAndRedirect.bind(this)} className="u-w-100 u-medium-left" type="secondary">
                                    {this.props.descriptions.cancelOrderButton || "Anuluj zamówienie"}
                                </OraButton>
                            </div>
                            <div className="l-col l-col-6 "> 
                                {this.canAcceptVerification() &&
                                    <OraButton onClick={this.gotoNextCheckoutStepButton.bind(this)} className="u-w-100 u-medium-right" type="primary">
                                        {this.props.descriptions.acceptDepositButton || "Akceptuję kaucję"}
                                    </OraButton>
                                }
                            </div>
                             <div className="l-col l-col-6 ">
                                {!this.canAcceptVerification() && this.isPaytelAcceptable() &&
                                    <OraButton onClick={this.handleRepaymentButton.bind(this)} className="u-w-100 u-medium-right" type="primary">
                                        {"Kontynuuj"}
                                    </OraButton>
                                }
                            </div>
                        </div>
                   </div> 
                ]}
            </div>
        )
    }

    canAcceptVerification = () => {
        const category = this.props.errors[0].category;
        const channel = this.props.channels.sales;
        return category === "CV_MAGNUM_WITH_DEPOSIT" && (channel === "POS" || channel === "AKA");
    }
}

const renderSeparatorLine = () => {
    return (
        <div className="o-separator u-padding-top"/>
    )
}

let mapStateToProps = state => ({
    errors: getCvMagnumCheckoutErrors(state),
    noOfBundles: getMaxBundleNo(state),
    channel: getSalesChannel(state)
});

let mapDispatchToProps = dispatch=> ({
    gotoNextCheckoutStep: () => dispatch(gotoNextCheckoutStep()),
    dismiss: () => dispatch(dismissCvErrors()),
    goToCartSummary: () => dispatch(gotoPreviousCheckoutStep()),
    removeFromCartAndRedirect: () => dispatch(removeFromCartAndRedirect()),
    handleRepayment: () => dispatch(handleRepayment())
});

export default connect(mapStateToProps, mapDispatchToProps)(MagnumCustomerVerificationModalView);

